import { Route, Switch } from "react-router-dom";
import MainLayout from "../components/mainLayout";
import MainLayoutWithoutSidebar from "../components/mainLayoutWithoutSidebar";
import { authRoutes } from "./auth.routes";
import { mainRoutesList, mainRoutesWithoutSidenavList } from "./main.routes";
import { Redirect } from "react-router";
import { getPermissions } from "../util/auth.util";
import { IRouteItem } from "../interfaces";

function Routes() {
  const permissions = getPermissions();
  const filterPermittedPaths = (routes: IRouteItem[]) => {
    return routes.filter(route => (!route.permission || (route.permission && permissions?.includes(route.permission))))
  }

  return (
    <Switch>
      {authRoutes}

      <Route
        exact
        path={filterPermittedPaths(mainRoutesList).map((item) => item.path)}
        component={MainLayout}
      />

      <Route
        exact
        path={filterPermittedPaths(mainRoutesWithoutSidenavList).map((item: any) => item.path)}
        component={MainLayoutWithoutSidebar}
      />

      <Redirect to="/pagenotfound" />

    </Switch>
  );
}

export default Routes;
