import { Layout } from "antd";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Redirect, Switch } from "react-router";
import { mainRoutes } from "../routes/main.routes";
import { toogleDrawer } from "../store/actions/authActionCreators";
import { isUserLoggedIn } from "../util/auth.util";
import Header from "./header";
import Navdrawer from "./navdrawer";
import Sidenav from "./sidenav";

const { Content } = Layout;

function MainLayout() {
  const dispatch = useDispatch();
  const [sidebarColllapse, setSidebarColllapse] = useState<boolean>(false);
  const [colorTheme, setColorTheme] = useState<boolean>(false);

  const { visible } = useSelector((state: any) => state.authReducer);

  const onClose = () => {
    dispatch(toogleDrawer(visible));
  };
  const hideDrawer = () => {
    dispatch(toogleDrawer(visible));
  };

  const handleSidebarTrigger = () => {
    setSidebarColllapse(!sidebarColllapse);
  };

  const handleThemeChange = () => {
    setColorTheme(!colorTheme);
  };

  useEffect(() => {
    if (colorTheme) {
      document.body.classList.add("dark");
      document.body.classList.remove("light");
    } else {
      document.body.classList.add("light");
      document.body.classList.remove("dark");
    }
  }, [colorTheme]);

  if (!isUserLoggedIn()) {
    return <Redirect to="/login" />;
  }

  return (
    <Layout>
      <Header changeTheme={handleThemeChange} onClose={onClose} />
      <Sidenav
        sidebarColllapse={sidebarColllapse}
        handleSidebarTrigger={handleSidebarTrigger}
      />
      <Navdrawer onClose={onClose} visible={visible} hideDrawer={hideDrawer} />
      <Content
        className={`page__wrapper ${sidebarColllapse ? "expanded" : ""}`}
      >
        <Switch>
          {mainRoutes}
        </Switch>
      </Content>
    </Layout>
  );
}

export default MainLayout;
