import {
  DashboardOutlined,
  TableOutlined,
  DotChartOutlined,
  SettingOutlined,
  UserAddOutlined,
  CiCircleOutlined,
  EnvironmentOutlined,
  UserOutlined,
  BookOutlined,
} from "@ant-design/icons";
import { NavLinks } from "../interfaces";


export const projectNavList = (projectSlug: string) => [
  { href: "/", icon: <DashboardOutlined />, title: "Project Dashboard" },
  {
    href: `/${projectSlug}/requirement-management`,
    icon: <BookOutlined />,
    title: "Requirement Management",
  },
  {
    href: `/${projectSlug}/test-case`,
    icon: <DotChartOutlined />,
    title: "Test Case",
  },
  {
    href: `/${projectSlug}/test-plan`,
    icon: <TableOutlined />,
    title: "Test Plan",
  },
  {
    href: `/${projectSlug}/settings`,
    icon: <SettingOutlined />,
    title: "Settings",
    children: [
      {
        href: `/${projectSlug}/settings/members`,
        icon: <UserAddOutlined />,
        title: "Project Members",
        permission: "projectmember.read"
      },
      {
        href: `/${projectSlug}/settings/cycle`,
        icon: <CiCircleOutlined />,
        title: "Cycle",
      },
      {
        href: `/${projectSlug}/settings/environment`,
        icon: <EnvironmentOutlined />,
        title: "Environment",
      },
    ],
  },
];

export function adminNavList(projectSlug: string): NavLinks[] {
  return [
    {
      href: "/admin",
      icon: <SettingOutlined />,
      title: "Overview",
      children: [
        { href: "/admin/users", icon: <UserOutlined />, title: "Users", permission: "user.read" },
      ],
    },
  ];

}

