import React, { useState } from 'react';
import { ThemeList } from '../enum/enum';
import { getTheme, setTheme as setThemeLocal } from '../util/localStorage.util';

export const ThemeContext = React.createContext({ toggleTheme: () => { }, theme: ThemeList.DARK });

interface Props {
    children: React.ReactNode
}

export const ThemeProvider = (props: Props) => {
    const [theme, setTheme] = useState(getTheme() || ThemeList.DARK);
    const changeTheme = (newTheme: ThemeList) => {
        setTheme(newTheme);
        setThemeLocal(newTheme);
    }
    const toggleTheme = () => {
        if (theme === ThemeList.Light) changeTheme(ThemeList.DARK);
        else changeTheme(ThemeList.Light);
    };

    return (
        <ThemeContext.Provider value={{ theme, toggleTheme }}>
            {props.children}
        </ThemeContext.Provider>
    )
}
