import { Provider } from "react-redux";
import { BrowserRouter } from "react-router-dom";
import { ErrorBoundary } from "./hoc/ErrorBoundary";
import Routes from "./routes";
import { store } from "./store/store";
import { useContext, useEffect } from "react";
import { ThemeList } from "./enum/enum";
import "./assets/sass/dark.main.less";
import PageNotFound from "./assets/images/error-404.png";
import { Detector } from "react-detect-offline";
import { ThemeContext } from "./providers/ThemeProvider";

function App() {
  const { theme } = useContext(ThemeContext);

  useEffect(() => {
    if (theme === ThemeList.Light) {
      require("./assets/sass/main.less");
    } else {
      require("./assets/sass/dark.main.less");
    }
  }, [theme]);

  return (
    <Detector
      polling={{
        url: 'https://ipv4.icanhazip.com',
        enabled: true,
        interval: 30000,
        timeout: 0
      }}
      render={({ online }) => {
        if (online) {
          return (
            <BrowserRouter>
              <ErrorBoundary>
                <Provider store={store}>
                  <Routes />
                </Provider>
              </ErrorBoundary>
            </BrowserRouter>
          );
        } else {
          return (
            <div className="error__page__wrapper">
              <div className="page__inside">
                <div>
                  <h1>No internet</h1>
                  <p>We've noticed you lost your internet connection !</p>
                </div>
                <img src={PageNotFound} alt="" />
              </div>
            </div>
          );
        }
      }}
      onChange={(data) => { }}
    />
  );
}

export default App;
