import { Menu, Typography } from "antd";
import Sider from "antd/lib/layout/Sider";
import SubMenu from "antd/lib/menu/SubMenu";
import { Link, useLocation } from "react-router-dom";
import { useRouteMatch } from "react-router";
import { NavLinks } from "../interfaces";
import { adminNavList, projectNavList } from "./sideNavList";
import { useEffect, useState } from "react";
import { MenuFoldOutlined, MenuUnfoldOutlined } from "@ant-design/icons";
import { getPermissions } from "../util/auth.util";
interface IProps {
  sidebarColllapse: boolean;
  handleSidebarTrigger: () => void;
}

function Sidenav({ sidebarColllapse, handleSidebarTrigger }: IProps) {
  const location = useLocation();

  const route = useRouteMatch<{ projectSlug: string }>();

  const projectSlug = route.params.projectSlug;
  const [isHidden, setIsHidden] = useState(false);
  const [mode, setMode] = useState<any>(
    sidebarColllapse ? "vertical" : "inline"
  );
  const permissions = getPermissions();

  const filterPermittedNavs = (routes: NavLinks[]) => {
    return routes.filter(route => (!route.permission || (route.permission && permissions?.includes(route.permission))))
  }

  const composeNavLinks = () => {
    if (route.path.startsWith("/admin"))
      return adminNavList(projectSlug)
    return projectNavList(projectSlug)
  }

  useEffect(() => {
    setMode(sidebarColllapse ? "vertical" : "inline");
  }, [sidebarColllapse]);

  return (
    <Sider
      trigger={
        <Menu>
          <Menu.Item
            onClick={handleSidebarTrigger}
            className="btn__sider__trigger"
            icon={
              sidebarColllapse ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />
            }
          >
            {!sidebarColllapse && "Collapse Sidebar"}
          </Menu.Item>
        </Menu>
      }
      collapsible
      collapsed={sidebarColllapse}
      breakpoint="lg"
      onBreakpoint={(broken) => {
        console.log(broken);
        if (broken) return setIsHidden(true);
        return setIsHidden(false);
      }}
      hidden={isHidden}
    >
      <Typography.Text strong>TCMS</Typography.Text>
      <Menu
        mode={mode}
        defaultOpenKeys={["/" + location.pathname.split("/")[1]]}
        defaultSelectedKeys={[location.pathname]}
        theme="light"
      >
        {filterPermittedNavs(composeNavLinks()).map((navLink: NavLinks) => {
          if (navLink.children) {

            return (
              <SubMenu
                icon={navLink.icon}
                key={navLink.href}
                title={navLink.title}
              >
                {filterPermittedNavs(navLink.children).map((childrenLink: NavLinks) => (
                  <Menu.Item key={childrenLink.href} icon={childrenLink.icon}>
                    <Link to={childrenLink.href}>{childrenLink.title}</Link>
                  </Menu.Item>
                ))}
              </SubMenu>
            );
          }
          return (
            <Menu.Item key={navLink.href} icon={navLink.icon}>
              <Link to={navLink.href}>{navLink.title}</Link>
            </Menu.Item>
          );
        })}
      </Menu>
    </Sider>
  );
}

export default Sidenav;
