import * as authActions from "../actions/authActions";
import { GET_PROJECT_DETAILS } from "../actions/types/projectTypes";

const initState: any = {
  projectDetails: null,
};

export const projectReducer = (state = initState, action: any): any => {
  switch (action.type) {
    case GET_PROJECT_DETAILS:
      return {
        ...state,
        projectDetails: action.payload,
      };

    default: {
      return state;
    }
  }
};
